/* eslint-disable prettier/prettier */
import React, { useState, useEffect } from "react"
import { useCookies } from "react-cookie"
import { useSpring, animated } from "react-spring"
import "./reset.css"
import { PropTypes } from "prop-types"
import BodyClassName from "react-body-classname"
import Styles from "./layout.module.css"
import Header from "./header/header"
import Footer from "./footer/footer"
import VisibilityContext from "../context/visibility"

const Layout = ({ children }) => {
    const cookieConfig = {
        name: "intro",
        timeout: 3600,
    }
    const [intro, setIntro] = useState(false)
    const [bodyVisibility, setBodyVisibility] = useState(false)
    const [cookies, setCookie] = useCookies([cookieConfig.name])
    const bodyAnimation = useSpring({
        opacity: bodyVisibility ? 1 : 0,
    })

    useEffect(() => {
        if (!cookies[cookieConfig.name]) {
            setIntro(true)
            setCookie(cookieConfig.name, true, {
                maxAge: cookieConfig.timeout,
            })
        } else {
            setBodyVisibility(true)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <BodyClassName className={!bodyVisibility ? "is-locked" : ""}>
            <VisibilityContext.Provider value={bodyVisibility}>
                <div className={Styles.wrapper}>
                    <Header
                        intro={intro}
                        setBodyVisibility={setBodyVisibility}
                    />
                    <animated.div style={bodyAnimation}>
                        <main>{children}</main>
                        <Footer />
                    </animated.div>
                </div>
            </VisibilityContext.Provider>
        </BodyClassName>
    )
}

export default Layout

Layout.defaultProps = {
    children: "<div/>",
}

Layout.propTypes = {
    children: PropTypes.node,
}
