import React, { useContext } from 'react'
import delay from 'delay'
import { Link } from 'gatsby'
import { useSpring, animated, config } from 'react-spring'
import { bool, func } from 'prop-types'
import VisibilityContext from '../../context/visibility'
import ThemeSwitch from '../themeswitch/themeswitch'
import Title from './title'
import Styles from './header.module.css'

const Header = ({ intro, setBodyVisibility }) => {
	const bodyVisibility = useContext(VisibilityContext)
	const wrapperAnimation = useSpring({
		to: async next => {
			await delay(1500)
			await next({
				minHeight: '100px',
				config: { mass: 1, tension: 250, friction: 30, clamp: true },
			})
		},
		from: {
			minHeight: `${
				typeof window !== 'undefined' ? window.innerHeight : 0
			}px`,
		},
	})

	const titleAnimation = useSpring({
		to: async next => {
			await delay(250)
			await next({
				opacity: 1,
				transform: 'translateY(0px)',
				config: config.slow,
			})
			await next({
				flexGrow: 0,
				config: { mass: 1, tension: 250, friction: 30, clamp: true },
			})
		},
		from: { opacity: 0, transform: 'translateY(50px)', flexGrow: 1 },
		onRest: () => {
			setBodyVisibility(true)
		},
	})

	const themeSwitchAnimation = useSpring({
		opacity: bodyVisibility ? 1 : 0,
	})

	return (
		<animated.header
			className={Styles.section}
			style={intro ? wrapperAnimation : null}
		>
			<div className={`container ${Styles.wrapper}`}>
				<Link to="/" className={Styles.link}>
					<div className={`${Styles.header}`}>
						<animated.div style={intro ? titleAnimation : null}>
							<Title />
						</animated.div>
					</div>
				</Link>
				<animated.div
					className={Styles.themeSwitch}
					style={themeSwitchAnimation}
				>
					<ThemeSwitch />
				</animated.div>
			</div>
		</animated.header>
	)
}

Header.propTypes = {
	intro: bool.isRequired,
	setBodyVisibility: func.isRequired,
}

export default Header
